._1Lxpd {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

._2SrV2 {
  width: 63px;
  height: 30px;
  border-width: 1px;
  border-color: #121212;
  border-style: solid;
  border-radius: 20px;
  cursor: pointer;
  position: relative;
  transition: 0.3s;
}

._5CjZJ {
  width: 26px;
  height: 26px;
  border-radius: 20px;
  background: #121212;
  margin: 2px 3px;
  transition: 0.4s ease-in-out;
  z-index: 100;
}

._aZqFb,
._NR7_U {
  font-size: 23px;
  position: absolute;
  top: 4px;
  color: #fdc13c;
}

._NR7_U {
  right: 3.3px;
  z-index: -1;
}

._aZqFb {
  top: 0px;
  left: 3.3px;
  transform: rotate(240deg);
  z-index: -1;
}

._3QbTe ._5CjZJ {
  transform: translateX(31px);
  background: #efefef;
}

._3QbTe._2SrV2 {
  background: #121212;
  z-index: 1;
}

/* =========== Small ========== */
._2SrV2._1YEqb {
  width: 48px;
  height: 20px;
}

._1YEqb ._5CjZJ {
  width: 18px;
  height: 18px;
  margin: 1px 2px;
}

._1YEqb._3QbTe ._5CjZJ {
  transform: translateX(26px);
}

._1YEqb ._aZqFb,
._1YEqb ._NR7_U {
  font-size: 16px;
  top: 2px;
}

._1YEqb ._aZqFb {
  top: 0px;
}
